.news{
  width: 23%;
  padding: 20px;
  /* background-color:rgb(161, 166, 171); */
  display: flex;
  flex-direction: column;
  row-gap:10px ;
  border: 1px solid gray;
}
.news-image{
  width: 100%;
  height: 200px;
  background-color: white;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.news-image img{
  max-width: 100%;
}

.news-articles{
  width: 100%;
  padding: 30px 5%;
  /* background-color: white; */
  display: flex;
  column-gap: 2%;
  flex-wrap: wrap;
  row-gap: 20px;
}
.header{
  width: 100%;
  padding: 12px 5%;
  background-color:rgb(52, 119, 178);
  color: white;
  display: flex;
  justify-content: space-between;
}
.header input{
  width: 28%;
  padding: 10px;
  font-size: 17px;
  outline: none;
  border: none;
  height: 38px;
}
.source{
  display: flex;
  justify-content: space-between;
  color: gray;
}

@media(max-width:786px){
  .news{
    width: 100%;
    height: 50%;
  }
  .header input {
    width: 55%;
  }
  #news-heading{
    font-size: 28px;
  }
}